/*
*   表头获取和配置的公用方法
*		使用方法:
*   引入：
*   import tableHeadSet from '@/mixins/tableHeadSet'
*   配置:
*   mixins: [tableHeadSet]
*/

export default {
  data() {
    return {
      formIdent: '', // 可配置表单的唯一标识
      tableData: [], // 表格数据
      columnList: [], // 表头配置
      expandList: [], // 扩展字段
      tableColumnSortData: [],
      labels: {}, // 字段名称
      refreshKey: 1 // el-table刷新的key
    }
  },
  methods: {
    /*
    * 接口获取的表头和本地设置的表头进行合并
    * 本页面配置的表头  columnSets
    * 接口给回的表头 apiHeadSets
    * isExtend 是否拓展字段
    * isShow 是否显示
    */
    initTableHeadMixins(apiHeadSets = [], module = 'word', columnSetsName = 'columnSets', width = 100) {
      // 将 columnSets 和 apiHeadSets 合并，生成总的头部设置
      const totalHeadSets = apiHeadSets.map(i => {
        return {
          // isExtend: i.extend,
          // isShow: i.show,
          // headerAlign: i.headAlign,
          // isDict: i.dict,
          // isForceShow: i.forceShow,
          // extend: i.extend,
          isParam: false,
          ...i,
          width: module === 'input' && !i.width ? width : i.width,
          module
        }
      }).map(item => {
        const c = this[columnSetsName].find(i => i.prop === item.prop)
        return c ? { ...item, ...c } : { ...item }
      })

      // 筛选扩展字段
      this.$nextTick(() => {
        if (module === 'word') {
          this.expandList = totalHeadSets.filter(f => f.extend && f.show)
        }
      })

      /*
      *   返回表头配置
      *   1. 会将本地columnSets设置与api返回的apiHeadSets进行合并，本地属性将会覆盖远程属性
      *   2. 执行顺序，先合并并覆盖属性，再进行过滤，将过滤show和extend属性
      */
      this.$nextTick(() => {
        this.refreshKey += 1
      })
      return totalHeadSets.filter(f => f.show && !f.extend)
    }
  }
}
