import request from '@/apis/request.wms'

// 列表
export function getListApi(data = {}) {
  return request({
    url: '/basic/supplier/pageList',
    method: 'post',
    data
  })
}
// 下拉列表
export function getPullListItemApi(data = {}) {
  return request({
    url: '/basic/supplier/listItem',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data = {}) {
  return request({
    url: '/basic/supplier/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data = {}) {
  return request({
    url: '/basic/supplier/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data = {}) {
  return request({
    url: '/basic/supplier/delete',
    method: 'post',
    data
  })
}

